
import {schedulingInfo} from '@/api/scheduling'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

import baseLoading from 'components/base-loading.vue';

export default {
components: {
Datetime,
Group,
baseLoading
},
name: 'scheduling',
data () {
return {
isLoadingData: true,
choseDay: "",         //
hrScheduleDate: "",
choseFlag: false,
scheduledDate: [],    //
}
},
methods: {
change(){
this.isLoadingData = true;
this.changeDay = this.choseDay.slice(0, 4) + "-" + this.choseDay.slice(5, 7);
this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.changeDay});
},
lastMonth(){
let nowMonth = parseInt(this.choseDay.slice(5, 7));
let nowYear = parseInt(this.choseDay.slice(0, 4));
if (nowMonth === 1) {
nowMonth = 12;
this.choseDay = (nowYear - 1) + "" + nowMonth + "";
this.hrScheduleDate = (nowYear - 1) + "-" + nowMonth
} else if (nowMonth <= 10) {
this.choseDay = nowYear + "" + "0" + (nowMonth - 1) + "";
this.hrScheduleDate = nowYear + "-" + "0" + (nowMonth - 1);
} else {
this.choseDay = nowYear + "" + (nowMonth - 1) + "";
this.hrScheduleDate = nowYear + "-" + (nowMonth - 1);
}
this.isLoadingData = true;
this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.hrScheduleDate});
},
nextMonth(){
let nowMonth = parseInt(this.choseDay.slice(5, 7));
let nowYear = parseInt(this.choseDay.slice(0, 4));
if (nowMonth === 12) {
nowMonth = 1;
this.choseDay = (nowYear + 1) + "" + "0" + nowMonth + "";
this.hrScheduleDate = (nowYear + 1) + "-" + "0" + nowMonth;
} else if (nowMonth >= 9) {
this.choseDay = nowYear + "" + (nowMonth + 1) + "";
this.hrScheduleDate = nowYear + "-" + "0" + (nowMonth + 1);
} else {
this.choseDay = nowYear + "" + "0" + (nowMonth + 1) + "";
this.hrScheduleDate = nowYear + "-" + "0" + (nowMonth + 1);
}
this.isLoadingData = true;
this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.hrScheduleDate});
},
getScheduling(params){
schedulingInfo(params).then(response => {
if (response.data && response.data.rows) {
this.scheduledDate = response.data.rows;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
getNowTime(item){
let date = new Date();
let year = date.getFullYear();
let month = date.getMonth() + 1;
let strDate = date.getDate();
if (month >= 1 && month <= 9) {
month = "0" + month;
}
if (strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
let nowTime = year + "-" + month + "-" + strDate;
if (item === nowTime) {
this.loadData();
return true;
} else {
return false;
}
},
loadData() {
setTimeout(() => {
if (document.getElementById('toDay')) {
let container = document.getElementById('toDay').offsetTop - 5;
let x = this.$refs.my_scroller.scrollTo(0, container, true);
}
}, 500);
},
},
mounted() {
this.loadData();
},
created(){

let nowYear = new Date().getFullYear();
let nowMonth = new Date().getMonth() + 1;
if (nowMonth < 10) {
nowMonth = "0" + nowMonth;
}
this.choseDay = nowYear + "" + nowMonth + "";
this.hrScheduleDate = nowYear + "-" + nowMonth;
this.getScheduling({"hrEmpId": 12229, "hrScheduleDate": this.hrScheduleDate});
},
}




